class MarvelService {
  #apiBse = "https://gateway.marvel.com:443/v1/public/";
  #apiKey = "apikey=cf276086cdf6be7ac54faf8b479f7071";
  #baseOffset = 210;

  getResource = async (url) => {
    let res = await fetch(url);

    if (!res.ok) {
      // console.error(`Could not fetch ${url}, status ${res.status}`)
      throw new Error(`Could not fetch ${url}, status ${res.status}`)
    }

    return await res.json();
  }

  getAllCharacter = async (offset = this.#baseOffset) => {
    const res = await this.getResource(`${this.#apiBse}characters?limit=9&offset=${offset}&${this.#apiKey}`)
    return res.data.results.map(this.#transformCharacter);
  }

  getCharacter = async (id) => {
    const res = await this.getResource(`${this.#apiBse}characters/${id}?${this.#apiKey}`)
    return this.#transformCharacter(res.data.results[0]);
  }

  #transformCharacter = (char) => {
    return {
      id: char.id,
      name: char.name,
      description: char.description ? `${char.description.slice(0, 210)}...` : 'There is no description for this character',
      thumbnail: `${char.thumbnail.path}.${char.thumbnail.extension}`,
      homepage: char.urls[0].url,
      wiki: char.urls[1].url,
      comics: char.comics.items,
    }
  }
}

export default MarvelService;